@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&family=Josefin+Sans:wght@100;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body * {
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{padding-top:80px}.hide{display:none}.change-background{background-color:#035203 !important}li{list-style:none}.purchase .add,.cart button{width:90px;padding-top:.4em;padding-bottom:.4em;font-weight:bold;font-family:'Josefin Sans', sans-serif;border:1px solid #bbb;border-radius:3px;background-color:#ccc}.purchase .add:hover,.cart button:hover{cursor:pointer;background-color:#bbb}nav{height:80px;position:fixed;top:0;left:0;right:0;display:flex;background-color:#023102}nav a{width:200px;height:100%;margin:0 5px;display:flex;align-items:center;justify-content:center;text-decoration:none;color:#fff;border:1px solid #035203;border-radius:10px}nav .active{text-decoration:underline;background-color:#035203}.banner{width:750px;margin-top:100px;margin-left:auto;margin-right:auto;display:grid;grid-template-columns:repeat(3, 250px);grid-template-rows:repeat(3, 150px)}.banner div[class^='image']{background-size:cover;border-radius:3px}.banner .image-2{grid-row:2 / 3;grid-column:2 / 3}.banner .image-3{background-position:0 -10px;grid-row:3 / 4;grid-column:3 / 4}.banner p{align-self:center;text-align:center;font-size:25px;font-family:'Josefin Sans', sans-serif}.banner .p-1{grid-column:2 / 4}.banner .p-2{grid-column:1 / 3;grid-row-start:3}.banner .p-3{transform:rotateZ(-20deg);font-feature-settings:"smcp";font-variant:small-caps;font-size:20px;font-weight:bold}.cart-link{width:100px;height:70px;position:fixed;top:5px;right:5px;display:flex;justify-content:center;align-items:center;font-size:25px;font-family:'Josefin Sans', sans-serif;color:#fff;border-radius:5px;border:1px solid #035203;background-color:#023102}.cart-link span{margin-left:8px;font-size:15px}.cart-link:hover{cursor:pointer}.items{margin:0;padding-top:60px;padding-left:200px}.items li{width:600px;margin-bottom:20px;padding:10px;display:grid;grid-template-columns:250px 1fr;grid-gap:10px;list-style:none;border:1px solid #ccc;border-radius:10px}.items li img{width:250px;border-radius:5px}.items li .item-specs{padding-left:25px;display:grid;align-content:space-between;border-left:1px solid #ccc}.info h2{font-feature-settings:"smcp";font-variant:small-caps}.info p{padding-left:.5em}.info .type{font-style:italic}.info .price{font-weight:700}.purchase .quantity{width:90px;margin-bottom:12px;padding-left:10px;font-size:20px;font-family:'Josefin Sans', sans-serif}.cart{position:fixed;z-index:10;top:80px;right:0;width:300px;padding:10px;border-radius:5px;border:1px solid black;background-color:#fff}.cart table{margin-bottom:20px;border-collapse:collapse;width:100%}.cart th{padding-top:.4em;padding-bottom:.4em;text-align:right;font-size:16px;font-family:'Cutive Mono', monospace}.cart th:first-child{text-align:left;width:30%}.cart tr:last-child{width:60%}.cart tr:last-child th{padding-top:.8em;border-top:1px solid #aaa}

